body {
	font-family: 'Roboto';
	font-size: 17px;
}

h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

h2 {
  font-size: 1.99rem;
}

input, button {
	border: 0;
	&:focus {
		outline: 0;
		border: 0;
	}
}

a {
	text-decoration: none;
}

.container {
    @include container();
    @include sm(max-width, rem(510));
    @include md(max-width, rem(690));
    @include lg(max-width, rem(930));
    @include xl(max-width, rem(1110));
}